/* for Firefox */
@-moz-keyframes scrollText {
    from { 
        -moz-width: 50%;
        -moz-opacity: .25;
    }
    20% {
        -moz-opacity: .5f;
    }
    60% { 
        -moz-width: 80%;
        -moz-opacity: .1;
    }
    to { 
        -moz-width: 120%;
        -moz-opacity: .05;
    }
}

/* for Chrome */
@-webkit-keyframes scrollText {
    from { 
        -webkit-width: 20%;
        -webkit-opacity: .5;
    }
    20% {
        -webkit-opacity: .5f;
    }
    to { 
        -webkit-width: 120%;
        -webkit-opacity: .5;
    }
}

@keyframes scrollText {
    from { 
        width: 50%;
        opacity: .01;
    }
    10% { 
        opacity: .1;
    }
    20% {
        opacity: .5f;
    }
    60% { 
        opacity: .2;
    }
    70% { 
        width: 80%;
        opacity: .02;
    }
    to { 
        width: 120%;
        opacity: .01;
    }
}

