.game-player-shadowmap {
    position: absolute;
    background-image: url("./images/player/Playershadowmap.png");
    pointer-events: none;
    opacity: .91;
    z-index: 13;
}

.game-player-bloom-inner {
    position: absolute;
    background-image: url("./images/player/Playerinnerbloom.png");
    opacity: .5;
    z-index: 9;
    -moz-animation: bloomdim .15s linear infinite;
    -webkit-animation: bloomdim .15s linear infinite;
    animation: bloomdim .15s linear infinite;
    pointer-events: none;
}

.game-player-bloom-middle {
    position: absolute;
    background-image: url("./images/player/Playermiddlebloom.png");
    opacity: .9;
    z-index: 10;
    -moz-animation: bloombright .35s linear infinite;
    -webkit-animation: bloombright .35s linear infinite;
    animation: bloombright .35s linear infinite;
    pointer-events: none;
}

.game-player-bloom-outter {
    position: absolute;
    background-image: url("./images/player/Playeroutterbloom.png");
    opacity: .9;
    z-index: 11;
    -moz-animation: bloomdim .25s linear infinite;
    -webkit-animation: bloomdim .25s linear infinite;
    animation: bloomdim .25s linear infinite;
    pointer-events: none;
}

@keyframes bloomdim {
    0%{
        opacity: .1;
    }
    50%{
        opacity: .5;
    }
    100%{
        opacity: .1;
    }
}
@keyframes bloombright {
    0%{
        opacity: .3;
    }
    50%{
        opacity: .5;
    }
    100%{
        opacity: .3;
    }
}

.game-npc-level-container {
    position: absolute;
    z-index: 6;
}

.game-npc-container {
    position: absolute;
    z-index: 6;
}

.game-npc-avatar {
    position: absolute;
    background-image: url("./images/npc/Robotavatar.png");
    z-index: 7;
}

.game-npc-fresnel {
    position: absolute;
    background-image: url("./images/npc/Robotfresnel.png");
    z-index: 6;
}








